var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('v-card',{staticClass:"pa-4 ma-4",attrs:{"flat":""}},[_c('v-card-title',[_c('v-flex',{attrs:{"sm6":"","offset-sm3":"","xs12":""}},[_c('h3',{staticClass:"headline mb-0",staticStyle:{"text-align":"center","font-size":"16px","font-family":"Tahoma, Geneva, sans-serif","white-space":"nowrap"}},[_vm._v(" General Information For All Hunts ")])])],1),_c('v-card-text',{staticStyle:{"color":"black","font-family":"Tahoma, Geneva, sans-serif","font-size":"16px"},attrs:{"sm10":"","offset-sm1":"","xs12":""}},[_c('div',{staticStyle:{"color":"black"}},[_c('p',[_c('strong',[_vm._v("Hunts include:")]),_vm._v(" Lodging – We have a base camp with bunkhouse-style accommodations. It is not a fancy 5-star lodge, but suitable for a hunting camp! *See more below! Meals and beverages – Three meals and basic beverages are provided. Transportation to and from Rapid City Regional Airport – please just be in contact with us if you plan to fly so we can accommodate flight times. Full or Semi-guided hunting - depending on the hunt booked. All of our rifle deer hunts are fully guided – no exceptions! Alcoholic beverages within reason - if you require or want something very specific, you may want to bring that yourself! ")]),_c('p',[_c('strong',[_vm._v("Hunts do NOT include:")]),_vm._v(" Licenses – We have a lottery draw system. We will assist you through the application process, but the cost of the license is up to you! *See more below! Taxes required by the state of South Dakota – The current state sales tax rate is 6%. Taxidermy services – We have a taxidermist available if needed, or you are welcome to take your trophy home to your own taxidermist. Meat processing – We have the option of someone who can process meat, or facilities for you to do it yourself if wanted. Shipping of trophies or meat – Most guys prefer to take home with them, if you want to ship, we will help if possible. Gratuity for Guides & Staff Firearms and ammunition – These can be available at reasonable cost with advance notice. ")])])])],1),_c('v-card',{staticClass:"pa-4 ma-4",attrs:{"flat":""}},[_c('v-card-title',[_c('v-flex',{attrs:{"sm6":"","offset-sm3":"","xs12":""}},[_c('h3',{staticClass:"headline mb-0",staticStyle:{"text-align":"center"}},[_vm._v("Non Hunters")])])],1),_c('v-card-text',{staticStyle:{"color":"black","font-family":"Tahoma, Geneva, sans-serif","font-size":"16px"},attrs:{"sm10":"","offset-sm1":"","xs12":""}},[_c('div',{staticStyle:{"color":"black"}},[_vm._v(" Non-hunters are welcome, just please let us know if you plan on bringing a non-hunting companion as it may depend on our accommodation availability! Please ask for the current pricing for a non-hunting companion. ")])])],1),_c('v-card',{staticClass:"pa-4 ma-4",attrs:{"flat":""}},[_c('v-card-title',[_c('v-flex',{attrs:{"sm6":"","offset-sm3":"","xs12":""}},[_c('h3',{staticClass:"headline mb-0",staticStyle:{"text-align":"center"}},[_vm._v(" Accommodations ")])])],1),_c('v-card-text',{staticStyle:{"color":"black","font-family":"Tahoma, Geneva, sans-serif","font-size":"16px"},attrs:{"sm10":"","offset-sm1":"","xs12":""}},[_c('div',{staticStyle:{"color":"black"}},[_vm._v(" Our accommodations are not what you would see in a 5-star resort, but it is also not a tent camp in the wilderness. We have small bunkhouse style cabins (some with electric heat, some with a wood stove). The beds do have bedding, but we recommend that you bring a sleeping bag for extra warmth if you have room. Besides the bunkhouse style cabins, the upstairs of Tomilyn’s house is used for lodging. There is a detached shower house with two complete bathrooms - there are towels provided. For husband/wife couples, we usually have them stay in a spare bedroom in the main house or in a camper for privacy. We serve meals in a garage attached to our house that is just a short distance from where you will be staying. We also have a pool table that you are more than welcome to use while socializing. We do our best to meet everyone's needs for accommodations, but if you have a special need, please let us know early so we can prepare! We are in the process of updating our accommodations, but life is busy, so it may be a few years until the work is complete! ")])])],1),_c('v-card',{staticClass:"pa-4 ma-4",attrs:{"flat":""}},[_c('v-card-title',[_c('v-flex',{attrs:{"sm6":"","offset-sm3":"","xs12":""}},[_c('h3',{staticClass:"headline mb-0",staticStyle:{"text-align":"center"}},[_vm._v(" Licensing/Tags ")])])],1),_c('v-card-text',{staticStyle:{"color":"black","font-family":"Tahoma, Geneva, sans-serif","font-size":"16px"},attrs:{"sm10":"","offset-sm1":"","xs12":""}},[_c('div',{staticStyle:{"color":"black"}},[_vm._v(" SD does have a lottery draw system with preference points. Preference points are $5 for a resident and $10 for a nonresident per drawing. License costs vary depending on the application and the species. "),_c('br'),_c('ul',[_c('li',[_c('b',[_vm._v("Rifle deer")]),_vm._v(" – We have two separate drawings: West River Special Buck and West River Deer. West River Special Buck is a private land only tag and good for any unit west of the Missouri River except the Black Hills and reservation units. The West River Deer drawing is for the same deer and same season, but it can be used on private or public land and is unit specific. It currently takes around two to three years to draw the mule deer tag and around one to two for a whitetail tag. West River Special applications are generally due mid April. West River Deer applications are due mid June. Preference points can be purchased from Sept 15 through Dec 15. ")]),_c('li',[_c('b',[_vm._v("Archery deer")]),_vm._v(" – Archery deer tags are unlimited for hunters on private land. You do still have to “apply” for the license, but it is practically a guarantee. ")]),_c('li',[_c('b',[_vm._v("Turkeys")]),_vm._v(" – Turkey licenses are hard to draw on the first round. Most of our hunters draw in the third round or pick up their tags in the leftovers. Applications are generally due mid February and the third round is mid March. It is not a guarantee, but we have never had a turkey hunter not get a license. Preference points can be purchased in years prior up to Dec 15. ")]),_c('li',[_c('b',[_vm._v("Antelope")]),_vm._v(" – Antelope licenses are hard to draw. Similar to the rifle deer, there is a Special Antelope drawing for private land only tags. It usually tags a point or two to draw antelope. Timelines are the same as deer for applications. ")])])])])],1),_c('v-card',{staticClass:"pa-4 ma-4",attrs:{"flat":""}},[_c('v-card-title',[_c('v-flex',{attrs:{"sm6":"","offset-sm3":"","xs12":""}},[_c('h3',{staticClass:"headline mb-0",staticStyle:{"text-align":"center","white-space":"nowrap"}},[_vm._v(" FAQ ")])])],1),_c('v-card-text',{staticStyle:{"color":"black","font-family":"Tahoma, Geneva, sans-serif","font-size":"16px"},attrs:{"sm10":"","offset-sm1":"","xs12":""}},[_c('div',{staticStyle:{"color":"black"}},[_c('b',[_vm._v("How do I book a hunt?")]),_c('br'),_vm._v(" To book a hunt we require a deposit upon booking. You are not booked until a deposit is on file! Final payment is due upon arrival or 30 days prior to arrival. Deposits are nonrefundable. If you do not draw the license (and have proof of application and preference point purchases) we will refund your deposit or roll you forward to the next available year. To book your hunt, contact Tomilyn at 605-441-1685! "),_c('br'),_c('br'),_c('b',[_vm._v("What kind of success can I expect?")]),_c('br'),_vm._v(" Unless you are looking for a B&C trophy you can be assured of several chances at nice trophy animals. The rest is up to you. We generally have a 98-100% success rate on rifle deer, around 50% success on archery deer, 150-200% success on turkeys, and have never had an antelope hunter go home eating tag soup. "),_c('br'),_c('br'),_c('b',[_vm._v("What's the best firearm to bring?")]),_c('br'),_vm._v(" Big deer (and antelope) can be tough, we recommend a .270 or larger rifle, but more important is accuracy. Practice with your gun and know exactly where it is shooting. Practice, practice, practice. Obviously, on turkey hunts, most hunters already have a favorite shotgun, and on archery hunts, we hope you plan on bringing a bow. "),_c('br'),_c('br'),_c('b',[_vm._v("How do I get a license?")]),_c('br'),_vm._v(" We will send information on how to apply and deadlines. You will need to acquire preference points for multiple years before the year you plan to hunt. We help you through this process. Please pay attention to the information we send you as if you don’t get the proper points it can deter a hunt you’ve had booked for years. We do not have transferable landowner tags, so if you do not draw the proper tag through the SDGF&P, you are out of luck. "),_c('br'),_c('br'),_c('b',[_vm._v("What is your guarantee? ")]),_c('br'),_vm._v(" We guarantee 100% effort in assisting you to a trophy animal you will be proud of. We can't control weather, marksmanship, or luck. ")])])],1)],1)}
